.catalog__list {
  margin: 0;
  padding: 0;

  @media (min-width: @tablet-width) {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 27px 8.9%;
      padding: 0 4%;
  }

  @media (min-width: @desktop-width) {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      gap: 27px 80px;
      padding: 0;
  }
}

.catalog__card {
  display: block;
  margin: 0 auto;

  @media (min-width: @tablet-width) {
      width: auto;
  }
}

.catalog__item {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 0 6.25%;
  padding-top: 20px;
  padding-bottom: 24px;
  border-bottom: 1px solid @background-grey-2;

  @media (min-width: @tablet-width) {
      position: relative;
      background-color: @basic_grey;
      margin-top: 79px;
      margin-bottom: 34px;
      padding: 161px 11.5% 40px;
      border-bottom: none;
  }

  @media (min-width: @desktop-width) {
      padding: 163px 38.5px 40px;
      margin-bottom: 37px;
  }
}

.catalog__item--more {
  position: relative;
  padding-top: 78px;

  @media (min-width: @tablet-width) {
      padding: 159px 11.5% 37px;
      background-color: @white-txt;
      border: 2px solid @basic_grey;
  }

  @media (min-width: @desktop-width) {
      padding: 158px 37px 37px;
  }
}

.catalog__item--more::before {
  content: "";
  position: absolute;
  display: block;
  width: 30px;
  height: 2px;
  background-color: @border-grey-2;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 39px;

  @media (min-width: @tablet-width) {
      width: 60px;
      top: 81px;
  }
}

.catalog__item--more::after {
  content: "";
  position: absolute;
  display: block;
  width: 30px;
  height: 2px;
  background-color: @border-grey-2;
  transform: rotate(90deg);
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 38px;

  @media (min-width: @tablet-width) {
      width: 60px;
      top: 80px;
  }
}

.catalog__img {
  width: 50%;

  @media (min-width: @tablet-width) {
      width: 100%;
      bottom: 301px;
      position: absolute;
      display: block;
      left: 0;
      right: 0;
      margin: 0 auto;
  }

  @media (min-width: @desktop-width) {
      bottom: 310px;
  }
}

.catalog__wrapper {
  width: 50%;

  @media (min-width: @tablet-width) {
      width: 100%;
      margin-bottom: 19px;
  }
}

.catalog__title {
  font-family: @oswald-fnt;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: @special_dark-grey-2;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 14px;
  width: 76px;

  @media (min-width: @tablet-width) {
      font-size: 20px;
      line-height: 24px;
      width: 144px;
      text-align: center;
      margin: 0 auto;
      margin-bottom: 21px;
  }

  @media (min-width: @desktop-width) {
      position: relative;
      left: 3px;
      line-height: 21px;
      margin: 0 auto;
      margin-bottom: 25px;
  }
}

.catalog__title--more {
  width: 100%;
  text-align: center;
  margin-bottom: 8px;

  @media (min-width: @tablet-width) {
      width: 134px;
      padding: 0;
      margin-bottom: 22px;
  }

  @media (min-width: @desktop-width) {
      margin-bottom: 24px;
  }
}

.catalog__table {
  font-family: @arial-fnt;
  font-size: 12px;
  line-height: 16px;
  font-weight: 400;
  color: @special_dark-grey;
  margin: 0;
  padding: 0;
  margin-bottom: 22px;

  @media (min-width: @tablet-width) {
      font-size: 16px;
      line-height: 16px;
  }
}

.catalog__text {
  font-family: @arial-fnt;
  font-size: 12px;
  line-height: 16px;
  font-weight: 400;
  color: @special_dark-grey;
  margin: 0;
  text-align: center;
  margin-bottom: 21px;

  @media (min-width: @tablet-width) {
      font-size: 16px;
      line-height: 18px;
      width: 167px;
      margin: 0 auto;
      margin-bottom: 85px;
  }

  @media (min-width: @desktop-width) {
      margin-bottom: 85px;
  }
}

.catalog__table-wrap {
  display: flex;
  justify-content: space-between;

  @media (min-width: @tablet-width) {
      margin-bottom: 16px;
  }
}

.catalog__table-definition {
  margin: 0;
}

.catalog__button {
  font-size: 16px;
  line-height: 20px;
}
