.page-footer {
  background-color: @basic_grey;

  @media (min-width: @desktop-width) {
      position: relative;
  }
}

.page-footer__wrapper {
  padding: 0 6.25%;
  text-align: center;

  @media (min-width: @tablet-width) {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 3.9%;
      padding-bottom: 45px;
      padding-top: 38px;
  }

  @media (min-width: @desktop-width) {
      max-width: 1440px;
      padding: 0 110px;
      padding-bottom: 45px;
      padding-top: 42px;
      max-width: 1440px;
      margin: 0 auto;
  }
}

.page-footer__contacts-wrapper {
  @media (min-width: @desktop-width) {
      max-width: 1440px;
      margin: 0 auto;
      padding: 0 110px;
      position: relative;
  }
}

.page-footer__contacts {
  padding: 27px 6.25%;
  display: flex;
  justify-content: space-between;
  background-color: @white-txt;

  @media (min-width: @tablet-width) {
      padding: 65px 9.25%;
      padding-bottom: 60px;
  }

  @media (min-width: @desktop-width) {
      position: absolute;
      width: 570px;
      left: 110px;
      top: 95px;
      padding-top: 65px;
      padding-bottom: 59px;
      padding-left: 5.25%;
      padding-right: 6%;
  }
}

.page-footer__contacts--grey {
  background-color: @basic_grey;

  @media (min-width: @desktop-width) {
      background-color: @white-txt;
  }
}

.page-footer__slogan {
  font-family: @oswald-fnt;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
  display: block;
  width: 140px;
  margin: 0;

  @media (min-width: @tablet-width) {
      font-size: 20px;
      line-height: 25px;
      width: 171px;
  }
}

.page-footer__adres {
  font-family: @arial-fnt;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  color: @special_dark-grey;
  display: block;
  width: 140px;
  margin: 0;

  @media (min-width: @tablet-width) {
      font-size: 16px;
      line-height: 20px;
      width: 159px;
      text-align: right;
  }

  @media (min-width: @desktop-width) {
      text-align: left;
  }
}

.page-footer__street {
  @media (min-width: @tablet-width) {
      display: block;
      margin-bottom: 17px;
  }
}

.page-footer__logo {
  display: block;
  padding-top: 39px;
  padding-bottom: 27px;
  border-bottom: 1px solid #d9d9d9;

  &:hover {
      opacity: 0.8;
  }

  &:active {
      opacity: 0.6;
  }

  @media (min-width: @tablet-width) {
      padding: 0;
      margin: 0;
      border-bottom: none;
      margin-right: 32px;
      padding-top: 8px;
  }
}

.page-footer__list {
  list-style: none;
  margin: 0;
  padding: 3px 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.page-footer__item {
  @media (min-width: @tablet-width) {
  }
}

.page-footer__social {
  border-bottom: 1px solid @border-grey;

  @media (min-width: @tablet-width) {
      border-bottom: none;
  }
}

.page-footer__copyright {
  padding: 21px 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (min-width: @tablet-width) {
      padding: 0;
      margin: 0;
  }
}

.page-footer__copyright-name {
  font-family: @arial-fnt;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: @special_dark-grey;
  display: block;

  @media (min-width: @tablet-width) {
      margin-right: 23px;
  }
}

.page-footer__copyright-img {
  width: auto;
  height: 34px;
}

.page-footer__copyright:hover .page-footer__copyright-img path {
  fill: @basic_green;
}
.page-footer__copyright:active {
  opacity: 0.3;
}

.page-footer__link {
  width: 56px;
  height: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.page-footer__link:hover path {
  fill: @basic_green;
}

.page-footer__link:active path {
  fill: @activ-green;
}

.page-footer__img {
  @media (min-width: @tablet-width) {
      width: auto;
      height: 23px;
  }
}

.page-footer__map {
  height: 362px;

  @media (min-width: @tablet-width) {
      min-height: 400px;
  }
}
